import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './index.scss'
export default class index extends Component {
    render() {
        return (
            <div className="home">
                <h1 className="title">React</h1>
                <div className="content"></div>
                <button>
                    <Link to="/list">List</Link>
                </button>
            </div>
        )
    }
}
